export default {
	name:'swperbanner',
	props:{
		List:Array,
	},
	data(){
		return{
			dotStyle: true,
			cardCur:0,
		}
	},
	methods:{
		cardSwiper(e) {
			this.cardCur = e.detail.current
		},
		gotoCurPage(e , p){
			// let datainfo = JSON.stringify(e)
			if(this.cardCur == p){
				let address = e.address
				let type= e.title
				uni.navigateTo({
					url: address +'?datainfo=' +type
				})
			}
		}
	}
}
